<template>

  <div style="display: flex; justify-content: center;">

    <div id="watchRequest">
      <div>
        <h1>
          Watch
        </h1>
      </div>

      <div>
        <label>classPattern: </label>
        <input type="text" v-model="watchRequestModel.classPattern">
      </div>

      <div>
        <label>methodPattern: </label>
        <input type="text" v-model="watchRequestModel.methodPattern">
      </div>

      <div>
        <label>express: </label>
        <input type="text" v-model="watchRequestModel.express">
      </div>

      <div>
        <label>conditionExpress: </label>
        <input type="text" v-model="watchRequestModel.conditionExpress">
      </div>

      <div>
        <label>isBefore: </label>
        <input type="checkbox" v-model="watchRequestModel.isBefore">
      </div>

      <div>
        <label>isFinish: </label>
        <input type="checkbox" v-model="watchRequestModel.isFinish">
      </div>

      <div>
        <label>isException: </label>
        <input type="checkbox" v-model="watchRequestModel.isException">
      </div>

      <div>
        <label>isSuccess: </label>
        <input type="checkbox" v-model="watchRequestModel.isSuccess">
      </div>

      <div>
        <label>expand: </label>
        <input type="number" v-model="watchRequestModel.expand">
      </div>

      <div>
        <label>sizeLimit: </label>
        <input type="number" v-model="watchRequestModel.sizeLimit">
      </div>

      <div>
        <label>isRegEx: </label>
        <input type="checkbox" v-model="watchRequestModel.isRegEx">
      </div>

      <div>
        <label>numberOfLimit: </label>
        <input type="number" v-model="watchRequestModel.numberOfLimit">
      </div>

      <div>
        <label>excludeClassPattern: </label>
        <input type="text" v-model="watchRequestModel.excludeClassPattern">
      </div>

      <div>
        <label>listenerId: </label>
        <input type="number" v-model="watchRequestModel.listenerId">
      </div>

      <div>
        <label>verbose: </label>
        <input type="checkbox" v-model="watchRequestModel.verbose">
      </div>

      <div>
        <label>maxNumOfMatchedClass: </label>
        <input type="number" v-model="watchRequestModel.maxNumOfMatchedClass">
      </div>

      <div>
        <label>jobId: </label>
        <input type="number" v-model="watchRequestModel.jobId">
      </div>

      <div style="display: flex; justify-content: space-between;">
        <button type="primary"  style="margin-right: 10px;" @click="sendWatchRequest"></button>
        <button type="primary"  style="margin-right: 10px;" @click="stopWatchRequest"></button>
        <button type="primary" @click="clear"></button>
      </div>
    </div>


