    <div id="objectRequest">
      <div>
        <h1>
          
        </h1>

        <p>watch,</p>
        <p></p>
        <p></p>
      </div>
      <div>
        <label>className: </label>
        <input type="text" v-model="objectQueryModel.className">
      </div>

      <div>
        <label>ClassLoaderHash: </label>
        <input type="text" v-model="objectQueryModel.classLoaderHash">
      </div>

      <div>
        <label>classLoaderClass: </label>
        <input type="text" v-model="objectQueryModel.classLoaderClass">
      </div>

      <div>
        <label>depth: </label>
        <input type="number" v-model="objectQueryModel.depth">
      </div>

      <div>
        <label>limit: </label>
        <input type="number" v-model="objectQueryModel.limit">
      </div>

      <div>
        <label>express: </label>
        <input type="text" v-model="objectQueryModel.express">
      </div>

      <div>
        <label>jobId: </label>
        <input type="number" v-model="objectQueryModel.jobId">
      </div>

      <div>
        <label>resultId: </label>
        <input type="number" v-model="objectQueryModel.resultId">
      </div>

      <div>
        <label>resultExpress: </label>
        <input type="text" v-model="objectQueryModel.resultExpress">
      </div>

      <div style="display: flex; justify-content: space-between;">
        <button type="primary"  style="margin-right: 10px;" @click="sendObjectRequest"></button>
      </div>
    </div>

    <div id="objectDisplay">
      <h1></h1>
      <h3 class="preserve-whitespace2">{{objectDisplay}}</h3>
      <Tree id="tree" :data="this.treeData"></Tree>
    </div>

  </div>

  <div>
    <h1>{{pwdResponse}}</h1>
  </div>
  <button type="primary" @click="sendPwdRequest">pwd</button><br>



  <table>
    <thead>
    <tr>
      <th>jobid</th>
      <th>resultid</th>
      <th>ts</th>
      <th>accessPoint</th>
      <th>className</th>
      <th>methodName</th>
      <th>cost</th>
      <th>value</th>
      <th></th> <!--  -->
    </tr>
    </thead>
    <tbody>
    <tr v-for="(item, index) in tableData" :key="index">
      <td>{{ item.jobId }}</td>
      <td>{{ item.resultId }}</td>
      <td>{{ item.ts }}</td>
      <td>{{ item.accessPoint }}</td>
      <td>{{ item.className }}</td>
      <td>{{ item.methodName }}</td>
      <td>{{ item.cost }}</td>
<!--      <td class="preserve-whitespace">-->
      <td>
        <Tree id="tree" :data="item.value"></Tree>
      </td>
      <td>
        <button @click="handleClick(item)"></button> <!--  -->
      </td>
    </tr>
    </tbody>
  </table>


</template>

<script>
// grpc_web

import {
  WatchClient,
  PwdClient,
  ObjectServiceClient
} from '@/assets/proto/ArthasServices_grpc_web_pb';

import { WatchRequest, ObjectQuery } from '@/assets/proto/ArthasServices_grpc_web_pb';
// import {
//   EchoServiceClient
// } from '@/assets/proto/echo_grpc_web_pb';
// import { EchoRequest } from '@/assets/proto/echo_pb';
import { Empty } from 'google-protobuf/google/protobuf/empty_pb';


export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'HelloWorld',
  data(){
    return {
      watchClient: null,
      pwdClient: null,
      objectClient: null,
      metadata: {},
      watchStream: null,
      watchRequestModel: {
        classPattern: "demo.MathGame",
        methodPattern: "primeFactors",
        express: "{params, target, returnObj}",
        conditionExpress: "",
        isBefore: false,
        isFinish: true,
        isException: false,
        isSuccess: false,
        expand: 2,
        sizeLimit: 10 * 1024 * 1024,
        isRegEx: false,
        numberOfLimit: 10,
        excludeClassPattern: "",
        listenerId: 0,
        verbose: false,
        maxNumOfMatchedClass: 50,
        jobId: 0
      },
      watchValueTreeData : [],
      objectRequestModel: {
        jobId: 0,
        resultId: 0,
        type: "",
        express: "{params, target, returnObj}",
        expand: 3
      },
      objectQueryModel: {
        className: "com.taobao.arthas.grpcweb.grpc.service.GrpcJobController",
        classLoaderHash: 0,
        classLoaderClass: "",
        express: "instances[0].{jobs}.get(0).get(1L).{listener}.{results}.get(0).get(1L)",
        depth: 2,
        limit: 1,
        jobId: 0,
        resultId: 0,
        resultExpress: "{params, target, returnObj}"
      },
      tableData: [], // 
      pwdResponse: "www",
      objectDisplay: "objectDisplay",
      treeData: [],
    };
  },


  created() {
    this.watchClient = new WatchClient('http://localhost:8567');
    this.pwdClient = new PwdClient('http://localhost:8567');
    this.objectClient = new ObjectServiceClient('http://localhost:8567')
    // this.echoclient = new EchoServiceClient('http://localhost:8567');
    // this.sendTest();
    this.metadata = {"Content-Type": "application/grpc-web-text"};
    this.sendObjectRequest()
  },

  methods:{
    handleNodeClick(data) {
      console.log(data);
    },
    sendPwdRequest(){
      var pwdRequest  = new Empty();
      this.pwdClient.pwd(pwdRequest, {}, (error, response) => {
        if (!error) {
          // 
          const jobId = response.getJobid();
          const type = response.getType();
          if(type == "pwd" && response.hasStringstringmapvalue()){
            var stringstringmapvalue = response.getStringstringmapvalue();
            var result = stringstringmapvalue.getStringstringmapMap().get("workingDir")
            this.pwdResponse = "jobid:" + jobId + "\n" + result
          }
        } else {
          // 
          console.error(error);
        }
      });
    },

    sendWatchRequest(){
      const watchRequest = new WatchRequest();
      watchRequest.setClasspattern(this.watchRequestModel.classPattern)
          .setMethodpattern(this.watchRequestModel.methodPattern)
          .setExpress(this.watchRequestModel.express)
          .setConditionexpress(this.watchRequestModel.conditionExpress)
          .setIsbefore(this.watchRequestModel.isBefore)
          .setIsfinish(this.watchRequestModel.isFinish)
          .setIsexception(this.watchRequestModel.isException)
          .setIssuccess(this.watchRequestModel.isSuccess)
          .setExpand(this.watchRequestModel.expand)
          .setSizelimit(this.watchRequestModel.sizeLimit)
          .setIsregex(this.watchRequestModel.isRegEx)
          .setNumberoflimit(this.watchRequestModel.numberOfLimit)
          .setExcludeclasspattern(this.watchRequestModel.excludeClassPattern)
          .setListenerid(this.watchRequestModel.listenerId)
          .setVerbose(this.watchRequestModel.verbose)
          .setMaxnumofmatchedclass(this.watchRequestModel.maxNumOfMatchedClass)
          .setJobid(this.watchRequestModel.jobId);

      this.watchStream = this.watchClient.watch(watchRequest,{});
      let _this = this
      // 
      this.watchStream.on('data', function(response) {
        const jobId = response.getJobid();
        const type = response.getType();
        const resultId = response.getResultid();
        if(type == "watch" && response.hasWatchresponse()){
          console.log("watch!!!!")
          const watchResponse = response.getWatchresponse();
          const data = _this.getObject(watchResponse.getValue());
          data['expand'] = true;
          const newData = {
            jobId: jobId,
            resultId: resultId,
            type: type,
            ts: watchResponse.getTs(),
            accessPoint: watchResponse.getAccesspoint(),
            className: watchResponse.getClassname(),
            methodName: watchResponse.getMethodname(),
            cost: watchResponse.getCost(),
            value: [data],
          };
          console.log(watchResponse.getValue())
          _this.tableData.unshift(newData);
          _this.watchRequestModel.jobId = jobId;
          // _this.stream = stream;
        }else {
          console.log("watchResponse: ----->")
          console.log('type:', type);
          console.log('message:', response.getStringvalue());
        }
      });

      this.watchStream.on('status', function(status) {
        console.log("status.code " + status.code);
        console.log("status.details " + status.details);
        console.log("status.metadata " + status.metadata.toString());
      });

      this.watchStream.on('end', function(end) {
        console.log("end: " + end)
        // stream end signal
        _this.watchStream.cancel()
      });

    },

    stopWatchRequest(){
      this.watchStream.cancel();
    },

    clear(){
      this.tableData = []
    },
    handleClick(item) {
      // item
      this.objectQueryModel.jobId = item.jobId;
      this.objectQueryModel.resultId = item.resultId;
      this.objectQueryModel.type = item.type;
      this.objectQueryModel.express = "instances[0].{jobs}.get(0).get(" + item.jobId + "L).{listener}.{results}.get(0).get(" + item.resultId + "L)"
    },

    // sendObjectRequest(){
    //   const objectRequest = new ObjectRequest();
    //   objectRequest.setJobid(this.objectRequestModel.jobId)
    //       .setResultid(this.objectRequestModel.resultId)
    //       .setType(this.objectRequestModel.type)
    //       .setExpress(this.objectRequestModel.express)
    //       .setExpand(this.objectRequestModel.expand)
    //
    //   this.objectClient.getInstance(objectRequest, {}, (error, response) => {
    //     if (!error) {
    //       // 
    //       const jobId = response.getJobid();
    //       const type = response.getType();
    //       const resultId = response.getResultid();
    //       if(response.hasJavaobject()){
    //         var javaobject = response.getJavaobject();
    //         this.objectDisplay ="type:" + type + " jobid: " + jobId + " resultId:"  + resultId + "\n" + javaobject
    //       }if(response.hasStringvalue()){
    //         var stringValue = response.getStringvalue();
    //         this.objectDisplay ="type:" + type + " jobid: " + jobId + " resultId:"  + resultId + "\n" + stringValue
    //       }
    //     } else {
    //       // 
    //       console.error(error);
    //     }
    //   });
    //
    // }
    sendObjectRequest(){
      const objectRequest = new ObjectQuery();
      objectRequest.setClassname(this.objectQueryModel.className)
          .setLimit(this.objectQueryModel.limit)
          .setDepth(this.objectQueryModel.depth)
          .setJobid(this.objectQueryModel.jobId)
          .setResultid(this.objectQueryModel.resultId)
          .setExpress(this.objectQueryModel.express)
          .setResultexpress(this.objectQueryModel.resultExpress)

      this.objectClient.query(objectRequest, {}, (error, response) => {
        if (!error) {
          this.treeData = []
          // 
          console.log("response", response)
          console.log("response.sucess", response.getSuccess())
          console.log("response.message", response.getMessage())
          const objectList = response.getObjectsList()
          objectList.forEach(item =>{
            const data = this.getObject(item);
            data['expand'] = true;
            this.treeData.push(data)
          })


          // if(response.hasJavaobject()){
          //   var javaobject = response.getJavaobject();
          //   this.objectDisplay ="type:" + type + " jobid: " + jobId + " resultId:"  + resultId + "\n" + javaobject
          // }if(response.hasStringvalue()){
          //   var stringValue = response.getStringvalue();
          //   this.objectDisplay ="type:" + type + " jobid: " + jobId + " resultId:"  + resultId + "\n" + stringValue
          // }
        } else {
          // 
          console.error(error);
        }
      });

    },

    displayBasicvalue(obj){
      if(obj.hasInt()){
        console.log(obj.getInt())
      }else if(obj.hasLong()){
        console.log(obj.getLong())
      }else if(obj.hasFloat()){
        console.log(obj.getFloat())
      }else if(obj.hasDouble()){
        console.log(obj.getDouble())
      }else if(obj.hasBoolean()){
        console.log(obj.getBoolean())
      }else if(obj.hasString()){
        console.log(obj.getString())
      }
    },

    displayArrayElements(obj){

      if(obj.hasObjectvalue()){
        console.log("JavaObject_className:", obj.getObjectvalue().getClassname())
        this.displayObject(obj.getObjectvalue())
      } else if(obj.hasBasicvalue()){
        const basicValue = obj.getBasicvalue()
        this.displayBasicvalue(basicValue)
      }else if(obj.hasArrayvalue()){
        console.log("arrayValue_className:", obj.getArrayvalue().getClassname())
        const elementsList = obj.getArrayvalue().getElementsList();
        elementsList.forEach(item=>{
          console.log("array_elements:")
          this.displayArrayElements(item)
        })
      }else if(obj.hasNullvalue()){
        console.log("NULL_className", obj.getNullvalue().getClassname())
      }else if(obj.hasUnexpandedobject()){
        console.log("unexpandedobject_className:", obj.getUnexpandedobject().getClassname())
      }
    },


    displayObject(obj){
      // console.log("className:", obj.getClassname())
      if(obj.hasObjectvalue()){
        console.log("JavaObject_className:", obj.getObjectvalue().getClassname())
        this.displayObject(obj.getObjectvalue())
      } else if(obj.hasBasicvalue()){
        const basicValue = obj.getBasicvalue()
        this.displayBasicvalue(basicValue)
      }else if(obj.hasArrayvalue()){
        console.log("arrayValue_className:", obj.getArrayvalue().getClassname())
        const elementsList = obj.getArrayvalue().getElementsList();
        elementsList.forEach(item=>{
          console.log("array_elements:")
          this.displayArrayElements(item)
        })
      }else if(obj.hasNullvalue()){
        console.log("NULL_className", obj.getNullvalue().getClassname())
      }else if(obj.hasCollection()){
        console.log("Collection_className:", obj.getCollection().getClassname())
        const javaObjectList = obj.getCollection().getElementsList()
        javaObjectList.forEach(item =>{
          this.displayObject(item)
        })
      }else if(obj.hasMap()){
        console.log("map_className:", obj.getMap().getClassname())
        const entriesList = obj.getMap().getEntriesList()
        entriesList.forEach(item =>{
          console.log("key:")
          this.displayObject(item.getKey())
          console.log("value:")
          this.displayObject(item.getValue())
        })
      }else if(obj.hasUnexpandedobject()){
        console.log("unexpandedobject_className:", obj.getUnexpandedobject().getClassname())
      }else if(obj.hasFields()){

        const fieldsList = obj.getFields().getFieldsList();
        fieldsList.forEach(item =>{
          console.log("files_name:", item.getName())
          this.displayObject(item)
        })
      }
    },


    getBasicvalue(obj){
      const aMap = {}
      let value;
      let type;
      if(obj.hasInt()){
        value = obj.getInt();
        type = "java.lang.Integer";
      }else if(obj.hasLong()){
        value = obj.getLong()
        type = "java.lang.Long";
      }else if(obj.hasFloat()){
        value = obj.getFloat()
        type= "java.lang.Float";
      }else if(obj.hasDouble()){
        value =obj.getDouble()
        type = "java.lang.Double";
      }else if(obj.hasBoolean()){
        value = obj.getBoolean()
        type = "java.lang.Boolean"
      }else if(obj.hasString()){
        value =obj.getString()
        type = "java.lang.String"
      }
      aMap['title'] = value + " (@" +type;
      return aMap
    },

    getArrayElements(obj){
      const aMap = {}
      let title = "element";
      try {
        title = obj.getName()
      }catch (e){
        try {
          title = obj.getClassname()
        }catch (e){
          console.log()
        }
      }
      if(obj.hasObjectvalue()){
        aMap['title'] = title +  " (@Object";
        aMap['children'] = []
        aMap['children'].push(this.getObject(obj.getObjectvalue()))
      } else if(obj.hasBasicvalue()){
        const basicValue = obj.getBasicvalue()
        if(title == "element" || this.isBasicType(title)){
          aMap['title'] = this.getBasicvalue(basicValue)['title'];
        }else{
          aMap['title'] = title + " (@Basic";
          aMap['children'] = []
          aMap['children'].push(this.getBasicvalue(basicValue))
        }
      }else if(obj.hasArrayvalue()){
        const arrayValue = obj.getArrayvalue()
        aMap['title'] = title + " (@ArrayList";
        aMap['children'] = []
        const elementsList = arrayValue.getElementsList();
        elementsList.forEach(item=>{
          aMap['children'].push(this.getArrayElements(item))
        })
      }else if(obj.hasNullvalue()){
        aMap['title'] = title + " (@null";
        aMap['children'] = []
        aMap['children'].push({"title":"(null)" + obj.getNullvalue().getClassname()})
      }else if(obj.hasUnexpandedobject()){
        aMap['title'] = title +" (@Unexpand";
        aMap['children'] = [];
        aMap['children'].push({"title":" (Unexpand) " +obj.getUnexpandedobject().getClassname()})
      }
      return aMap;
    },


    getObject(obj){
      const aMap = {}
      let title = "";
      try {
        title = obj.getName()
      }catch (e){
        try {
          title = obj.getClassname()
        }catch (e){
          console.log()
        }
      }

      if(obj.hasObjectvalue()){
        aMap['title'] = title +  " (@Object";
        aMap['children'] = []
        aMap['children'].push(this.getObject(obj.getObjectvalue()))
      } else if(obj.hasBasicvalue()){
        const basicValue = obj.getBasicvalue()
        if(title == "" || this.isBasicType(title)){
          aMap['title'] = this.getBasicvalue(basicValue)['title'];
        } else{
          aMap['title'] = title+ " (@Basic";
          aMap['children'] = []
          aMap['children'].push(this.getBasicvalue(basicValue))
        }
      }else if(obj.hasArrayvalue()){
        const arrayValue = obj.getArrayvalue()
        aMap['title'] =title +  " (@ArrayList";
        aMap['children'] = []
        const elementsList = arrayValue.getElementsList();
        elementsList.forEach(item=>{
          aMap['children'].push(this.getArrayElements(item))
        })
      }else if(obj.hasNullvalue()){
        aMap['title'] = title + " (@null";
        aMap['children'] = []
        aMap['children'].push({"title":"(@null)" + obj.getNullvalue().getClassname()})
      }else if(obj.hasCollection()){
        aMap['title'] = title+ " (@Collection";
        aMap['children'] = []
        const javaObjectList = obj.getCollection().getElementsList()
        javaObjectList.forEach(item =>{
          aMap['children'].push(this.getObject(item))
        })
      }else if(obj.hasMap()){
        aMap['title'] = title + " (@Map";
        aMap['children'] = [];
        const entriesList = obj.getMap().getEntriesList()
        entriesList.forEach(item =>{
          const bMap = {}
          const keyMap = {}
          const valueMap = {}
          bMap['title'] = "Entry"
          bMap['children'] = []
          keyMap['title'] = "key"
          keyMap['children']= []
          keyMap['children'].push(this.getObject(item.getKey()))
          bMap['children'].push(keyMap)
          valueMap['title'] = "value"
          valueMap['children']= []
          valueMap['children'].push(this.getObject(item.getValue()))
          bMap['children'].push(valueMap)
          aMap['children'].push(bMap)
        })
      }else if(obj.hasUnexpandedobject()){
        aMap['title'] = title +" (@Unexpand";
        aMap['children'] = [];
        aMap['children'].push({"title":" (@Unexpand) " + obj.getUnexpandedobject().getClassname()})
      }else if(obj.hasFields()){
        aMap['title'] = title;
        aMap['children'] = []
        const fieldsList = obj.getFields().getFieldsList();
        fieldsList.forEach(item =>{
          aMap['children'].push(this.getObject(item))
        })
      }
      return aMap;
    },

    isBasicType(type){
      if(type == "java.lang.String" || type == "java.lang.Integer" || type == "java.lang.Long"
          || type == "java.lang.Float" || type == "java.lang.Double" || type == "java.lang.Boolean"){
        return true;
      }
      return false;
    }

  }
}

</script>



<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

#watchRequest {
   display: flex;
